.my-talks-container {

  @media ($mnw: $xmdmx) {
    height: calc(100vh - 215px); } }

.my-talks {
  height: 100%;

  &__header {
    border-bottom: 1px solid $sharp-blue; }

  &__button {
    width: 140px;
    margin: 0 10px 10px 0;
    transition: all 0.3s ease;
    @media ($mnw: $xsdmn) and ($mxw: $xsdmx) {
      margin: 10px auto; } }

  &__pop-up {
    width: 600px; } }

.pagination {
  height: 25px;
  margin-top: 15px;
  display: flex;
  justify-content: space-between;

  @media ($mnw: $xsdmn) and ($mxw: $xmdmx) {
    display: none; }

  &__left-side {
    display: flex; }

  &__navi {
    margin: 0;
    font-family: "Oswald Light", sans-serif; }

  &__item-wrapper {
    display: flex; }

  &__item {
    width: 25px;
    height: 25px;
    margin-right: 5px;
    @include box-shadow (0, 0, 5px, 0, rgba(0,0,0,0.15));

    &:hover {
      cursor: pointer; }

    &_current {
      display: flex;
      justify-content: center;
      align-items: center; }

    &_fast-forward {
      background: url(assets/img/fast_forward.svg) no-repeat center;
      background-size: 50%; }

    &_forward {
      background: url(assets/img/forward.svg) no-repeat center;
      background-size: 50%; }

    &_fast-back {
      background: url(assets/img/fast_back.svg) no-repeat center;
      background-size: 50%; }

    &_back {
      background: url(assets/img/back.svg) no-repeat center;
      background-size: 50%; } }

  &__select {
    width: 70px;
    height: 25px;
    display: block;
    padding-left: 15px;
    margin-left: 20px;
    border: 0;
    @include box-shadow (0, 0, 5px, 0, rgba(0,0,0,0.15));
    font-family: Oswald Light, sans-serif; }

  &__per-page {
    font-family: "Oswald Light";
    margin-left: 20px; } }

.my-talk-settings {
  height: 80px;
  box-sizing: border-box;
  padding: 10px 0;
  display: block;

  @media ($mnw: $xsdmn) and ($mxw: $xsdmx) {
    height: 200px;
    padding: 0; }

  @media ($mnw: $sdmn) and ($mxw: $sdmx) {
    height: 120px; }

  &__filters {
    display: flex;
    flex-wrap: wrap; }

  &__title {
    text-transform: capitalize;
    line-height: 36px;
    margin: 20px 10px 0 0;
    font-size: 14px;
    @media ($mnw: $xsdmn) and ($mxw: $xsdmx) {
      margin: 0; } }

  &__button {
    width: 80px;
    height: 30px;
    margin: 22px 5px 0;
    border: 0;
    background: transparent;
    text-transform: capitalize;
    @include box-shadow (0, 0, 5px, 0, rgba(0,0,0,0.15));

    &:hover {
      background: $light-grey;
      cursor: pointer; }

    &:focus {
      outline: none; } }

  &__label {
    margin-bottom: 5px;
    font-size: 12px; }

  &__select {
    width: 130px;
    height: 30px;
    margin: 0 15px 0 0;
    border: 0;
    @include box-shadow (0, 0, 5px, 0, rgba(0,0,0,0.15));
    font-family: Oswald Light, sans-serif;

    &:focus {
      outline: none; }

    &:hover {
      cursor: pointer; }

    &_topic {
      width: 180px; }

    @media ($mnw: $xsdmn) and ($mxw: $xsdmx) {
      width: 100%;
      margin: 0; } }

  &__date-wrapper {
    box-sizing: border-box;
    @media ($mnw: $xsdmn) and ($mxw: $xmdmx) {
      display: none; } }

  &__date-label {
    font-size: 12px;
    box-sizing: border-box;
    padding-left: 15px; }

  &__date {
    margin: 0;
    position: relative; }

  &__select-wrapper {
    @media ($mnw: $xsdmn) and ($mxw: $xsdmx) {
      width: 100%;
      &_topic {
        margin-top: 5px; } } } }

